export default {
	data() {
		return {
			goodsList: [],
			goodsPrice: 0.0, // 商品金额
			sumPrice: 0.0, // 用户支付金额
			freight: 12.0 // 运费固定金额12
		}
	},
	onShow() {
		// 获取本地存储的数据
		uni.getStorage({
			key: "confirmList",
			success: (res) => {
				// console.log(res.data)
				this.goodsList = res.data
				// 遍历订单列表
				for (let i = 0; i < this.goodsList.length; i++) {
					// 商品金额 = 商品金额 + 每个商品的个数 * 每个商品的价格
					this.goodsPrice = this.goodsPrice + (this.goodsList[i].number * this.goodsList[i].price)
				}
				// 用户支付金额 = 商品金额 + 运费
				this.sumPrice = this.goodsPrice + this.freight;
			}
		})
	},
	filters:{
		// 优化商品金额计算小数点问题,保留小数点2位
		toFixed(x){
			return parseFloat(x).toFixed(2)
		}
	},
	// 页面回退，清除订单数据
	onBackPress(){
		this.clearOrder()
	},
	methods:{
		clearOrder(){
			uni.removeStorage({
				key:"confirmList",
				success:(res) => {
					this.goodsList = []
				}
			})
		}
	}
}
